import React, { useEffect, useState } from "react";
import axios from "axios";
import { beUrl } from "../../config";
import { formatKms } from "../common/helper";
import { Link, useHistory } from "react-router-dom";
import styled from "styled-components";

const AddButton = styled.span`
  position: fixed;
  z-index: 999999999999;
  right: 20px;
  bottom: 20px;
  cursor: pointer;
  font-size: 30px;
  color: white;
  background-color: blue;
  padding: 5px 13px;
  border-radius: 50%;
  text-align: center;
  border: 4px solid transparent;

  &:hover {
    color: blue;
    background-color: white;
  }
`;

const Inventory = (props) => {
  const [cars, setCars] = useState(undefined);
  const history = useHistory();

  useEffect(() => {
    async function fetchData() {
      const { data } = await axios.get(`${beUrl}/cars`);
      setCars(data);
    }
    fetchData();
  }, []);

  return cars ? (
    <>
      <AddButton onClick={() => history.push("/admin/new")}>
        <i className="fas fa-plus"></i>
      </AddButton>
      <table className="table table-hover">
        <thead>
          <tr>
            <th scope="col">ID</th>
            <th scope="col">Título</th>
            <th scope="col">Data Matrícula</th>
            <th scope="col">KMs</th>
            <th scope="col">Editar</th>
          </tr>
        </thead>
        <tbody>
          {cars.map((car) => {
            return (
              <tr key={car.id}>
                <td>{car.id}</td>
                <td>{car.title}</td>
                <td>{`${car.attributes.month.value} / ${car.attributes.year.value}`}</td>
                <td>{formatKms(car.attributes.kms.value)}</td>
                <td>
                  <Link to={`/admin/edit/${car.id}`}>
                    <i className="far fa-edit"></i>
                  </Link>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </>
  ) : (
    <h1>Loading...</h1>
  );
};

export default Inventory;
